ight-yellow: #fcd900;\n$bright-orange: #f79021; // !\n\n$undefined: #ff06b7;\n$number: #008be8;\n$string: #ff4d00;\n$boolean: #00aeaa;\n$function: #13a600;\n$event: $boolean;\n\n$purple: #57068c;\n\n$error-red-x-light: #ffd8d8;\n$error-red-light: #ef7373;\n$error-red: #f70f0f;\n$error-red-dark: #9b0000;\n\n$warn-yellow-x-light: #ffe8bb;\n$warn-yellow-light: #eec77a;\n$warn-yellow: #ffaa00;\n$warn-yellow-dark: #c08000;\n\n$opacity-default: 0.9;\n$opacity-mid: 0.75;\n$opacity-high: 0.5;\n$opacity-xh: 0.3;\n$opacity-xxh: 0.15;\n$opacity-xxx-h: 0.1;\n\n/* -------------------------------------------------------------------------- */\n\n$max-log-width: 20rem;\n$absolute-max-log-width: 32rem;\n$max-log-unexpanded-stream-height: 16rem;\n$max-log-wrapper-height-text: 32rem;\n$max-log-wrapper-height-shape: 32rem;\n\n$radius-xs: 0.15rem;\n$radius-small: 0.25rem;\n$radius-default: 0.375rem;\n$radius-large: 0.5rem;\n\n// PADDING\n$padding-xxs: 0.125rem;\n$padding-xs: 0.25rem;\n$padding-small: 0.5rem;\n$padding-mid: 0.9rem;\n$padding-large: 1rem;\n$padding-x-large: 1.5rem;\n$padding-xx-large: 2rem;\n$padding-xxx-large: 4rem;\n\n$scrollbar-width: $padding-xs;\n$scrollbar-width-large: $padding-small;\n\n// FONT\n$font-size-xs: 0.5rem;\n$font-size-small: 0.675rem;\n$font-size-default: 0.75rem;\n$font-size-large: 1rem;\n$font-size-x-large: 1.25rem;\n$font-size-xx-large: 1.5rem;\n\n$font-weight-thin: 400;\n$font-weight-default: 500;\n$font-weight-bold: 700;\n\n$transition-fast: 100ms;\n$transition-normal: 300ms;\n$transition-normal-slow: 500ms;\n$transition-slow: 700ms;\n$transition-highlight: 3000ms;\n\n$outline-light: 1px solid $light-grey;\n$outline-dark: 1px solid $black;\n\n$outline-highlight: 1px solid $elegant-red;\n\n$blur-small: 2px;\n$blur-default: 3px;\n$blur-high: 5px;\n$blur-xh: 7px;\n\n.prevent-pointer-events {\n  pointer-events: none;\n}\n\n/* -------------------------------------------------------------------------- */\n\n$rem-to-px: 16;\n\n@function unit-rem-to-px($value) {\n  @return calc($value / $rem-to-px);\n}\n\n/* -------------------------------------------------------------------------- */\n\n@keyframes select-text {\n  to {\n    user-select: text;\n  }\n}\n\n/* -------------------------------------------------------------------------- */\n\n.disabled {\n  pointer-events: none !important;\n  opacity: $opacity-high !important;\n}\n\n.up-front {\n  z-index: 999999999 !important;\n}\n\n.cursor-pointer {\n  cursor: pointer !important;\n}\n\n.cursor-crosshair {\n  cursor: crosshair !important;\n}\n\n.cursor-grab {\n  cursor: grab !important;\n}\n\n.cursor-grabbing {\n  cursor: grabbing !important;\n}\n\n.no-pointer-events {\n  pointer-events: none !important;\n\n  * {\n    pointer-events: none !important;\n  }\n}\n\n.trans-fast {\n  transition: all $transition-fast;\n}\n\n.rotate-180 {\n  transform: rotate(180deg);\n}\n\n/* -------------------------------------------------------------------------- */\n\n@mixin font-fixed {\n  font-family: 'Inconsolata', 'Courier New', Courier, monospace !important;\n}\n\n.font-fixed-width {\n  @include font-fixed;\n}\n\n.font-bold {\n  font-weight: $font-weight-bold !important;\n}\n\n.font-thin {\n  font-weight: $font-weight-thin !important;\n}\n\n/* -------------------------------------------------------------------------- */\n\n@mixin scrollbar($track-width, $track-color, $thumb-color, $thumb-color-hover) {\n  /* width */\n  &::-webkit-scrollbar {\n    width: $track-width;\n  }\n\n  /* Track */\n  &::-webkit-scrollbar-track {\n    background: $track-color;\n  }\n\n  /* Handle */\n  &::-webkit-scrollbar-thumb {\n    background: $thumb-color;\n  }\n\n  /* Handle on hover */\n  &::-webkit-scrollbar-thumb:hover {\n    background: $thumb-color-hover;\n  }\n}\n","@import 'root.scss';\n\n.hyper-log-host {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 0;\n  height: 0;\n  overflow: visible;\n\n  * {\n    margin: 0;\n    // padding: 0;\n    border: 0;\n    outline: none;\n    vertical-align: baseline;\n    text-rendering: optimizeLegibility;\n    -moz-osx-font-smoothing: grayscale;\n    font-kerning: none;\n\n    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,\n      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n    font-size: $font-size-default;\n    color: $black;\n  }\n}\n","@import 'root.scss';\n\n.hyper-log-streams-holder {\n  position: fixed;\n\n  display: flex;\n  flex-direction: column;\n  // flex-flow: column wrap-reverse;\n  gap: 0 $padding-large;\n\n  // width: 100%;\n  width: max-content;\n  height: min-content;\n  max-height: 100vh;\n  // overflow-y: scroll;\n  // overflow-x: visible;\n\n  transition: transform $transition-normal ease-in-out,\n    opacity $transition-normal ease-in-out;\n  pointer-events: none;\n\n  &.snap-holder {\n    width: min-content !important;\n  }\n}\n\n/* -------------------------------------------------------------------------- */\n\n.hyper-log-stream {\n  display: flex;\n  position: relative;\n  block-size: fit-content;\n\n  pointer-events: initial;\n\n  ////\n  // not sure if the optimization is needed\n  &.absolute-stream {\n    width: max-content;\n  }\n\n  &.stream-expand {\n    max-width: $absolute-max-log-width !important;\n\n    &:not(.shape-stream) .logs-wrapper {\n      width: max-content;\n      max-width: 60vw !important;\n    }\n  }\n  ////\n\n  &.shape-stream .logs-wrapper {\n    padding: 0;\n  }\n\n  .logs-wrapper {\n    position: relative;\n    display: flex;\n\n    margin: 0;\n    padding: 1px; // !important\n    // transition: width $transition-normal, height $transition-normal;\n\n    // pointer-events: none;\n\n    &.timeline-logs-wrapper:not(:has(.hyper-shape-log)),\n    &.timeline-logs-wrapper .hyper-log:not(.hyper-shape-log) {\n      max-width: $max-log-width !important;\n      width: $max-log-width !important;\n    }\n\n    scrollbar-width: none;\n    &::-webkit-scrollbar {\n      width: 0;\n      height: 0;\n      background: transparent;\n    }\n  }\n\n  &.stream-expand .logs-wrapper {\n    padding: $padding-xs calc($padding-xs + 1px - $scrollbar-width) $padding-xs\n      calc($padding-xs + 1px);\n    // z-index: -1;\n\n    border-top: 2px solid $grey;\n    border-bottom: 2px solid $grey;\n    background: $light-grey;\n\n    @include scrollbar($scrollbar-width, $light-grey, $grey, $dark-grey);\n  }\n\n  &.stream-expand:not(.shape-stream) .logs-wrapper {\n    gap: $padding-xs;\n    overflow: scroll;\n    max-height: $max-log-wrapper-height-text;\n  }\n\n  &.stream-expand.shape-stream .logs-wrapper {\n    gap: $padding-xxs;\n    overflow: scroll;\n    max-height: $max-log-wrapper-height-shape;\n  }\n\n  &.stream-horizontal {\n    flex-direction: column;\n  }\n\n  &.stream-vertical {\n    flex-direction: row;\n  }\n}\n","$expand-transition-time: $transition-normal;\n$expand-transition-function: cubic-bezier(0.15, 0.75, 0.25, 1);\n\n.hyper-log-timeline {\n  position: fixed;\n\n  display: flex;\n  flex-direction: column;\n\n  width: max-content;\n  max-height: 100vh;\n\n  margin: 0;\n  padding: 0;\n\n  transition: transform $transition-normal ease-in-out,\n    opacity $transition-slow ease-in-out;\n\n  .timeline-name {\n    flex-direction: row !important;\n    align-items: center !important;\n    justify-content: space-between !important;\n    flex-shrink: 0;\n\n    gap: $padding-x-large;\n    background: $light !important;\n\n    padding: 0 !important;\n    height: $padding-xx-large !important;\n\n    user-select: none;\n    border: none !important;\n\n    .pseudo-expander {\n      flex-grow: 1;\n      flex-shrink: 0;\n\n      height: 100%;\n    }\n\n    .timeline-name-text {\n      font-weight: 700;\n    }\n\n    .timeline-name-title {\n      width: 100%;\n      gap: 0;\n\n      flex-shrink: 1;\n      flex-grow: 1;\n\n      .timeline-move-icon {\n        margin: 0 $padding-small;\n      }\n    }\n\n    .timeline-name-icons {\n      height: 100% !important;\n      flex-grow: 1 !important;\n      flex-shrink: 0;\n\n      // ? is it ok?\n      svg {\n        height: 1em !important;\n        width: 1em !important;\n      }\n    }\n\n    .timeline-icon {\n      box-sizing: content-box !important;\n      padding: $padding-xs;\n\n      cursor: pointer;\n      pointer-events: initial !important;\n\n      &.timeline-back-icon {\n        transform: rotate(180deg);\n      }\n\n      &.timeline-restart-icon {\n        path {\n          fill: $elegant-green;\n        }\n\n        &:hover {\n          background: $elegant-green;\n\n          path {\n            fill: $white;\n          }\n        }\n      }\n\n      &.timeline-area-icon {\n        // path {\n        //   fill: $elegant-blue;\n        // }\n\n        &.timeline-area-enabled-icon {\n          path {\n            fill: $elegant-blue;\n          }\n        }\n\n        &:hover {\n          background: $elegant-blue !important;\n\n          path {\n            fill: $white !important;\n          }\n        }\n      }\n\n      &:hover {\n        &.timeline-back-icon,\n        &.timeline-fold-icon,\n        &.timeline-expand-icon {\n          background: $dark-grey;\n\n          path {\n            fill: $white;\n          }\n        }\n\n        &.timeline-pause-icon {\n          background: $elegant-yellow;\n\n          path {\n            fill: $white;\n          }\n        }\n      }\n    }\n  }\n\n  .timeline-scroll-wrapper {\n    height: 100%;\n    overflow-y: scroll;\n    // padding: 0 $padding-xs 0 0;\n    padding: 0;\n\n    // scroll-behavior: smooth;\n\n    background: rgba($white, $opacity-mid);\n    backdrop-filter: blur($blur-xh);\n\n    flex-grow: 1;\n\n    @include scrollbar($scrollbar-width-large, $white, $grey, $dark-grey);\n    &::-webkit-scrollbar {\n      // height: $scrollbar-width-large;\n      height: 0;\n    }\n\n    .timeline-background-alignment {\n      z-index: 0;\n      position: absolute;\n      height: 100%;\n      // width: 100%;\n      // background: rgba($grey, $opacity-xxh);\n      border-left: 2px solid $light;\n\n      &:hover {\n        border-left: 2px solid $grey;\n      }\n    }\n  }\n\n  .timeline-wrapper {\n    position: relative;\n\n    display: flex;\n    flex-direction: column;\n    // gap: $padding-xs;\n    flex-shrink: 0;\n    flex-wrap: nowrap;\n\n    // overflow-y: scroll;\n    padding: $padding-small 0 $padding-xs 0;\n  }\n\n  .timeline-stream-item-wrappers-header {\n    cursor: pointer;\n    transition: background-color $transition-normal ease-in-out,\n      border $expand-transition-time $expand-transition-function;\n\n    &.filtered-out-group-header {\n      // background: $dark-grey;\n\n      span {\n        color: $dark-grey !important;\n      }\n    }\n\n    span {\n      display: inline-block;\n      margin: 0 0 0 $padding-xs;\n      padding: $padding-small $padding-xs $padding-xs $padding-xs;\n\n      color: $grey;\n      font-size: $font-size-small;\n\n      transition: color $transition-normal ease-in-out;\n    }\n  }\n\n  .timeline-log-item-wrapper {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    gap: $padding-mid;\n\n    // z-index: 1;\n    padding: 0 $padding-xs 0 0;\n\n    transform-origin: center;\n    transition: max-height $transition-normal\n        cubic-bezier(0.645, 0.045, 0.355, 1),\n      border $expand-transition-time $expand-transition-function;\n    overflow: hidden;\n\n    .hyper-log-stream-in-time {\n      flex-shrink: 0;\n      padding: $padding-xxs 0 $padding-xxs $padding-xs;\n      margin: 0 0 0 $padding-xs;\n      // margin: 0;\n\n      .hyper-log:not(.hyper-shape-log) {\n        background: $white !important;\n        backdrop-filter: none;\n      }\n    }\n\n    .pseudo-expander {\n      flex-grow: 1;\n      flex-shrink: 0;\n\n      height: 1rem;\n    }\n\n    .timeline-timestamp {\n      @include font-fixed;\n      font-size: $font-size-small;\n      padding: $padding-xxs 0;\n\n      // flex-grow: 1;\n      flex-shrink: 0;\n      text-align: right;\n    }\n  }\n\n  /* -------------------------------------------------------------------------- */\n\n  .timeline-alignment-box {\n    position: absolute;\n    top: 0;\n    height: 100%;\n    z-index: -1;\n\n    // transition: left $transition-normal ease-in-out, opacity $transition-normal !important;\n    transition: left $expand-transition-time $expand-transition-function;\n\n    &.hidden-alignment-box {\n      opacity: 0;\n    }\n\n    &.indentation-box {\n      border-left: $padding-xxs solid $light;\n    }\n\n    &.declaration-box {\n      border-left: $padding-xs solid $light-grey;\n    }\n  }\n}\n","$dot-shadow-d: calc($padding-mid + $padding-small);\n$dot-d: $padding-mid;\n\n.graphics-host {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: 999;\n\n  .faded-graphics {\n    &.hyper-graphics-box {\n      // opacity: $opacity-xh;\n\n      * {\n        border: none !important;\n      }\n    }\n\n    // &.hyper-graphics-dot {\n    //   opacity: $opacity-high;\n    // }\n  }\n\n  /* -------------------------------------------------------------------------- */\n\n  .hyper-graphics-dot {\n    .the-dot-shadow {\n      width: $dot-shadow-d;\n      height: $dot-shadow-d;\n      border-radius: 50%;\n\n      position: absolute;\n      top: 0;\n      left: 0;\n      transform: translate(-50%, -50%);\n\n      backdrop-filter: blur($blur-small);\n      cursor: crosshair;\n\n      .the-dot {\n        width: $dot-d;\n        height: $dot-d;\n        border-radius: 50%;\n\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        transform: translate(-50%, -50%);\n\n        pointer-events: none;\n      }\n\n      /* -------------------------------------------------------------------------- */\n\n      // !\n\n      &.client-dot-shadow {\n        background: rgba($elegant-green, $opacity-mid);\n        .the-dot {\n          background: $elegant-green;\n        }\n      }\n\n      &.native-dot-shadow {\n        background: rgba($bright-orange, $opacity-mid);\n        .the-dot {\n          background: $bright-orange;\n        }\n      }\n\n      &:hover + .dot-description {\n        opacity: 1;\n      }\n    }\n\n    .dot-description {\n      left: calc($dot-shadow-d / 2 + $padding-xs);\n      transform: translateY(-50%);\n    }\n  }\n\n  /* -------------------------------------------------------------------------- */\n\n  .hyper-graphics-box {\n    pointer-events: none;\n\n    .the-box {\n      border-width: $padding-xxs;\n      border-style: solid;\n      box-sizing: border-box;\n\n      pointer-events: none;\n\n      &.client-box {\n        background: rgba($elegant-green, $opacity-high);\n        border-color: darken($bright-green, 15%);\n      }\n\n      &.native-box {\n        background: rgba($bright-orange, $opacity-high);\n        border-color: darken($bright-orange, 15%);\n      }\n\n      // .group-color-dot {\n      //   right: 0;\n      //   bottom: 0;\n      //   width: $padding-xs;\n      //   height: $padding-xs;\n      // }\n    }\n\n    &.client-graphics {\n      .box-info {\n        background: darken($bright-green, 15%);\n      }\n    }\n\n    &.native-graphics {\n      .box-info {\n        background: darken($bright-orange, 15%);\n      }\n    }\n\n    .box-info {\n      position: absolute;\n      top: 0;\n      right: 0;\n\n      display: flex;\n      flex-direction: column;\n      // gap: $padding-xs;\n      flex-wrap: nowrap;\n      flex-shrink: 0;\n\n      // padding: $padding-xs;\n\n      cursor: pointer;\n      pointer-events: initial;\n\n      .box-icon {\n        padding: $padding-xs;\n\n        &.icon-measures:hover {\n          background: $bright-orange;\n        }\n\n        &.icon-move {\n          cursor: move;\n        }\n      }\n\n      svg path {\n        fill: $white;\n      }\n\n      &:hover + .box-description {\n        opacity: 1;\n      }\n    }\n\n    .box-description {\n      left: 100%;\n    }\n  }\n\n  .graphics-description {\n    position: absolute;\n    top: 0;\n\n    display: flex;\n    flex-wrap: nowrap;\n    flex-direction: column;\n    align-items: flex-start;\n    justify-content: center;\n\n    color: $dark-mid-grey;\n    white-space: nowrap;\n\n    border-left-width: $padding-xs;\n    border-left-style: solid;\n\n    background: rgba($light, $opacity-mid);\n    backdrop-filter: blur($blur-xh);\n    padding: $padding-xs;\n\n    opacity: 0;\n    transition: opacity $transition-fast ease-in-out;\n    pointer-events: none;\n\n    max-width: 10rem;\n    white-space: wrap;\n    overflow: hidden;\n\n    pointer-events: none;\n\n    .description-key-word {\n      font-weight: $font-weight-bold;\n    }\n\n    * {\n      @include font-fixed;\n      font-size: $font-size-default;\n      line-height: 100%;\n      user-select: none;\n    }\n\n    /* -------------------------------------------------------------------------- */\n\n    // !\n\n    &.client-description * {\n      color: darken($elegant-green, 20%);\n    }\n\n    &.native-description * {\n      color: darken($bright-orange, 20%);\n    }\n  }\n}\n\n/* -------------------------------------------------------------------------- */\n\n@keyframes dot-animation-bounce-in {\n  0% {\n    transform: translate(-50%, -50%) scale(0.6);\n  }\n\n  60% {\n    transform: translate(-50%, -50%) scale(1.1);\n  }\n\n  100% {\n    transform: translate(-50%, -50%) scale(1);\n  }\n}\n\n.dot-animation-bounce-in {\n  animation: dot-animation-bounce-in $transition-fast ease-in;\n}\n\n/* -------------------------------------------------------------------------- */\n/* -------------------------------------------------------------------------- */\n\n.hyper-measures {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 1;\n\n  padding: 0;\n  margin: 0;\n\n  .measure-line-holder {\n    position: absolute;\n  }\n\n  .measure-line {\n    padding: 0;\n    margin: 0;\n    position: absolute;\n\n    width: 100%;\n    height: 100%;\n\n    background: rgba($light-grey, $opacity-high);\n    backdrop-filter: invert(1);\n    pointer-events: none;\n  }\n\n  .measure-line-text {\n    position: absolute;\n    white-space: nowrap;\n\n    padding: $padding-xs $padding-xs;\n    border-radius: $radius-small;\n    background: rgba($light, $opacity-mid);\n    backdrop-filter: blur($blur-xh);\n\n    @include font-fixed;\n    font-size: $font-size-small;\n    color: $black;\n\n    pointer-events: none;\n    user-select: none;\n  }\n}\n",".side-dragger-wrapper {\n  position: relative;\n\n  margin: 0;\n  padding: 0;\n\n  display: flex;\n  flex-direction: row;\n\n  overflow: hidden;\n\n  height: calc(100vh - $padding-xx-large);\n\n  .timeline-side-dragger {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n\n    padding: $padding-xs;\n    background: $white;\n\n    cursor: grab;\n\n    &:hover {\n      background: $light;\n    }\n\n    &.timeline-side-dragger-active {\n      cursor: grabbing;\n      background: $light !important;\n    }\n\n    svg {\n      pointer-events: none;\n      font-size: $font-size-default;\n      margin: 0;\n      transition: transform $transition-normal ease-in-out;\n\n      &.this-way-back {\n        transform: rotate(180deg);\n      }\n    }\n  }\n}\n\n/* -------------------------------------------------------------------------- */\n\n@keyframes width-expand {\n  0% {\n    width: 0;\n    background: $light-grey;\n  }\n\n  100% {\n    width: calc($font-size-default);\n    background: $white;\n  }\n}\n\n.animation-width-expand {\n  animation-duration: $transition-normal-slow;\n  animation-name: width-expand;\n}\n","// ! name\n\n.hyper-log-stream-name {\n  display: flex;\n  flex-direction: column;\n  // align-items: flex-start;\n  justify-content: center;\n  gap: $padding-xs;\n\n  background: rgba($light, $opacity-high);\n  backdrop-filter: blur($blur-xh);\n  box-shadow: 0 1px 0 0 $white;\n\n  border-left-width: $padding-xxs;\n  border-left-style: solid;\n  box-sizing: border-box;\n\n  width: 100%;\n\n  color: $black;\n  font-size: $font-size-small;\n  margin: 0;\n  padding: $padding-xs 0 $padding-xs 0;\n  white-space: nowrap;\n  user-select: none;\n\n  .display-name {\n    margin: 0 $padding-xs;\n\n    .log-total-counter {\n      @include font-fixed;\n      color: $grey;\n    }\n  }\n\n  & > div {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    gap: $padding-xs;\n\n    padding: 0;\n    margin: 0;\n  }\n\n  svg {\n    font-size: $font-size-small;\n    pointer-events: none;\n  }\n\n  .hyper-log-stream-name-relink {\n    &:hover {\n      cursor: crosshair;\n    }\n  }\n}\n\n.name-icon {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  width: calc($font-size-small + 2 * $padding-xxs);\n  height: calc($font-size-small + 2 * $padding-xxs);\n  // margin: -$padding-xxs;\n  padding: 0;\n  // border-radius: $radius-xs;\n\n  svg path {\n    fill: $light-grey;\n  }\n\n  &:hover {\n    background: $dark-grey;\n\n    svg path {\n      fill: $white !important;\n    }\n  }\n\n  &.active-icon {\n    svg path {\n      fill: $dark-grey;\n    }\n  }\n}\n\n.center-stage-holder {\n  margin: 0 $padding-xs !important;\n\n  .center-stage-id-copy {\n    cursor: pointer;\n    user-select: none;\n\n    &:active {\n      background: $bright-orange;\n    }\n  }\n\n  .stage-id-nav {\n    padding: 0;\n    margin: 0;\n\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n\n    .center-stage-id-part {\n      font-size: $font-size-small;\n      line-height: 100%;\n      padding: $padding-xxs 1px;\n\n      &:not(.center-stage-id-last) {\n        cursor: alias;\n\n        &:hover {\n          background: $dark-grey;\n          color: $white;\n        }\n      }\n    }\n  }\n\n  &.center-stage-holder-time {\n    display: flex !important;\n    align-items: center !important;\n    justify-content: flex-start;\n    gap: $padding-xs;\n\n    margin: 0 0 $padding-xs 0;\n\n    * {\n      margin: 0;\n      padding: 0;\n    }\n  }\n}\n\n// .center-stage-holder-time {\n\n//   .stage-id-nav {\n//     font-size: $font-size-small;\n//     line-height: 100%;\n//   }\n\n//   .center-stage-id-copy {\n//     display: inline;\n//   }\n// }\n\n/* -------------------------------------------------------------------------- */\n\n// ! menu\n\n$menu-height: calc($font-size-small + $padding-small * 2 + $padding-xxs * 2);\n$menu-height-text: calc(\n  $font-size-small + $padding-xs * 2 + $padding-xxs * 3 + $font-size-xs\n);\n\n@mixin menu-item-special-config($color) {\n  svg path {\n    fill: $color;\n  }\n  &:hover {\n    background: $color;\n    svg path {\n      fill: $white;\n    }\n  }\n}\n\n.hyper-log-stream-menu {\n  position: relative;\n\n  display: flex;\n  align-items: center;\n  gap: $padding-xxs;\n\n  opacity: 0;\n  height: 0;\n  // overflow: hidden;\n  pointer-events: none;\n\n  transition-property: height, margin, opacity;\n  transition-delay: 0;\n  transition-duration: $transition-fast;\n  transition-timing-function: ease-in-out;\n\n  // margin: 0;\n  padding: 0;\n  // z-index: 9990;\n\n  // border-radius: calc($radius-small + $padding-xxs);\n  // background: rgba($light-grey, $opacity-high);\n\n  user-select: none;\n\n  &.menu-start {\n    justify-content: flex-start;\n  }\n\n  &.menu-end {\n    justify-content: flex-end;\n  }\n\n  &.stream-menu-horizontal {\n    width: 100%;\n    margin: 0 0 $padding-small 0;\n    flex-direction: row;\n  }\n\n  &.stream-menu-vertical {\n    height: 100%;\n    margin: 0 $padding-small 0 0;\n    flex-direction: column;\n  }\n\n  .stream-menu-item {\n    display: flex;\n    flex-direction: row;\n    gap: $padding-xxs;\n    justify-content: center;\n    align-items: center;\n\n    position: relative;\n\n    color: $dark-grey;\n    background: $light-grey;\n\n    // margin: $padding-xxs;\n    // padding: $padding-xs $padding-small;\n    padding: $padding-small;\n    border-radius: $radius-small;\n\n    user-select: none;\n    cursor: pointer;\n\n    // tip\n    span {\n      // display: none;\n      position: absolute;\n      left: 50%;\n      top: calc(100% + $padding-xs);\n      transform: translateX(-50%);\n      padding: $padding-xs $padding-small;\n      border-radius: $radius-small;\n\n      font-size: $font-size-small;\n      font-weight: $font-weight-bold;\n      white-space: nowrap;\n      color: $dark-mid-grey;\n\n      background: rgba($white, $opacity-mid);\n      backdrop-filter: blur($blur-xh);\n\n      opacity: 0;\n      transition: opacity $transition-fast;\n\n      pointer-events: none;\n    }\n\n    &:hover {\n      span {\n        opacity: 1;\n      }\n    }\n\n    * {\n      user-select: none;\n    }\n\n    svg {\n      font-size: $font-size-small;\n    }\n\n    svg path,\n    svg circle {\n      fill: $dark-grey;\n    }\n\n    &.menu-expand-item,\n    &.menu-choosing-item {\n      background: $dark-grey !important;\n\n      svg path,\n      svg circle {\n        fill: $white;\n      }\n    }\n\n    &.menu-delete-item {\n      @include menu-item-special-config($elegant-red);\n    }\n\n    &.menu-pause-item {\n      @include menu-item-special-config($elegant-yellow);\n    }\n\n    &.menu-resume-item {\n      @include menu-item-special-config($elegant-green);\n    }\n\n    &.menu-have-graphics {\n      background: $dark-grey !important;\n\n      &.menu-graphics-single {\n        svg path,\n        svg circle {\n          fill: $white;\n        }\n      }\n\n      &.menu-graphics-all {\n        svg path,\n        svg circle {\n          fill: $bright-orange;\n        }\n      }\n    }\n\n    &.menu-already-center-staged {\n      background: $bright-yellow;\n\n      path {\n        fill: $white !important;\n      }\n\n      &:hover,\n      &.menu-choosing-item {\n        path {\n          fill: $bright-yellow !important;\n        }\n      }\n    }\n\n    // &.menu-un-center-staged-item,\n    // &.menu-un-center-staged-item:hover {\n    //   background: $bright-yellow;\n\n    //   path {\n    //     fill: $white;\n    //   }\n    // }\n\n    // &.special-menu-item {\n    //   margin-top: $padding-small;\n    // }\n\n    &:hover {\n      background: $grey;\n\n      // span {\n      //   display: block;\n      // }\n    }\n  }\n\n  // .item-divider {\n  //   width: 0;\n  //   height: 0;\n  //   margin: 0 $padding-xs;\n  // }\n}\n\n.stream-hovered,\n.stream-expand,\n.stream-current {\n  &:not(.in-grabbing) .hyper-log-stream-menu {\n    // .hyper-log-stream-menu {\n    pointer-events: initial !important;\n    opacity: 1 !important;\n\n    &.stream-menu-horizontal {\n      height: $menu-height !important;\n      // width: 100% !important;\n      margin: $padding-xs 0;\n    }\n\n    &.stream-menu-vertical {\n      width: $menu-height !important;\n      // height: 100% !important;\n      margin: 0 $padding-xs;\n    }\n  }\n}\n","@import 'root.scss';\n\n.hyper-log {\n  position: relative;\n\n  display: flex;\n  flex-direction: column;\n  flex-shrink: 0;\n\n  width: fit-content;\n  min-height: min-content;\n  transform-origin: top;\n\n  overflow: hidden;\n  pointer-events: initial;\n\n  &:not(.hyper-shape-log) {\n    background: rgba($white, $opacity-high);\n  }\n  backdrop-filter: blur($blur-high);\n\n  * {\n    pointer-events: initial;\n  }\n\n  &.log-in-history {\n    pointer-events: none;\n  }\n\n  &.log-current:not(.here-there-log) {\n    outline: $outline-dark;\n  }\n\n  &.here-there-log {\n    // overflow: hidden !important;\n    overflow: visible;\n\n    .hyper-log-body {\n      padding: $padding-small !important;\n      overflow: hidden !important;\n      scrollbar-width: none !important;\n      border-radius: $padding-xs;\n\n      outline-width: 1px;\n      outline-style: solid;\n\n      &::-webkit-scrollbar {\n        height: 0 !important;\n        width: 0 !important;\n      }\n    }\n  }\n\n  // &.log-expand {\n  //   margin-bottom: $padding-xs !important;\n  // }\n}\n\n.hyper-log-stream {\n  &:not(.stream-expand) {\n    .hyper-log-body {\n      max-width: $max-log-width;\n      max-height: $max-log-unexpanded-stream-height;\n      overflow: scroll;\n\n      scrollbar-width: none;\n\n      /* width */\n      &::-webkit-scrollbar {\n        scrollbar-gutter: stable both-edges;\n        position: absolute;\n        // width: $scrollbar-width;\n        height: $scrollbar-width;\n        background: transparent;\n      }\n\n      @include scrollbar($scrollbar-width, transparent, $grey, $dark-grey);\n\n      // &:has(.unfolded-inner) {\n      //   // width: $max-log-width !important;\n      // }\n    }\n  }\n}\n\n:not(.log-expand) {\n  .hyper-log-body {\n    padding: $padding-small calc($padding-small - $scrollbar-width)\n      calc($padding-small - $scrollbar-width) $padding-small;\n  }\n}\n\n.log-expand {\n  .hyper-log-body {\n    padding: $padding-small !important;\n  }\n}\n\n.hyper-log-body {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: flex-start;\n  gap: $padding-xxs;\n\n  padding: $padding-small;\n  overflow-wrap: break-word;\n\n  background: $light;\n  // background: rgba($light-grey, $opacity-default);\n\n  // user-select: all;\n  // &:focus {\n  //   animation: select 100ms step-end forwards;\n  // }\n\n  .hyper-log-body-header {\n    display: inline-flex;\n    gap: $padding-large;\n    justify-content: space-between;\n    align-items: center;\n\n    width: 100%;\n\n    &.timeline-log-body-header {\n      justify-content: flex-end;\n    }\n\n    .log-body-timestamp {\n      color: $grey;\n\n      display: inline-flex;\n      align-items: center;\n      justify-content: flex-start;\n      gap: $padding-xs;\n\n      svg {\n        font-size: $font-size-xs !important;\n      }\n    }\n\n    .source-location {\n      color: $dark-grey;\n    }\n\n    .log-body-timestamp,\n    .log-body-timestamp *,\n    .source-location {\n      @include font-fixed;\n      font-size: $font-size-small;\n      white-space: nowrap;\n    }\n  }\n}\n\n.hyper-log-body-content {\n  display: inline-flex;\n  align-items: flex-start;\n  flex-direction: row;\n  gap: $padding-small;\n\n  .hyper-log-center-stage {\n    flex-grow: 0;\n    flex-shrink: 0;\n    font-size: calc($font-size-xs + 2 * $padding-xxs);\n    padding: 0;\n\n    user-select: none;\n    cursor: pointer;\n\n    &:hover {\n      * {\n        fill: $grey;\n      }\n    }\n\n    &:active {\n      * {\n        fill: $dark-grey;\n      }\n    }\n  }\n\n  .hyper-log-count {\n    @include font-fixed;\n    color: $white;\n    background: rgba($black, $opacity-high);\n    font-size: $font-size-xs;\n    line-height: 100%;\n    white-space: nowrap;\n    // width: calc($font-size-small + $padding-xxs * 2);\n    padding: $padding-xxs $padding-xs;\n    // border-radius: 50%;\n\n    flex-shrink: 0;\n\n    display: flex;\n    justify-content: center;\n  }\n\n  .hyper-log-unit {\n    @include font-fixed;\n    // font-size: inherit;\n  }\n}\n\n/* -------------------------------------------------------------------------- */\n\n.shape-stream {\n  &.stream-expand {\n    .hyper-shape-log {\n      &:hover {\n        .shape-timestamp {\n          display: flex !important;\n        }\n      }\n    }\n  }\n\n  &.stream-vertical {\n    .hyper-shape-log {\n      align-items: flex-start;\n      justify-content: center;\n    }\n  }\n\n  .hyper-shape-log {\n    display: inline-flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: flex-start;\n\n    pointer-events: initial;\n\n    .shape-log-note {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      gap: $padding-small;\n\n      padding: 0 $padding-xs;\n\n      .shape-log-value {\n        cursor: pointer;\n        white-space: nowrap;\n      }\n\n      span {\n        @include font-fixed;\n\n        display: flex;\n        align-items: center;\n        gap: $padding-xs;\n\n        font-size: $font-size-small;\n        color: $light-grey;\n      }\n\n      .shape-timestamp {\n        display: none;\n      }\n\n      .shape-timestamp span {\n        font-size: $font-size-small;\n      }\n\n      svg {\n        font-size: $font-size-xs !important;\n\n        path {\n          fill: $light;\n        }\n      }\n    }\n  }\n}\n",".level-error {\n  &.hyper-log-stream {\n    .hyper-log-stream-name {\n      background: rgba($error-red-light, $opacity-xxh);\n    }\n\n    &.stream-expand .logs-wrapper {\n      border-top: 2px solid $error-red;\n      border-bottom: 2px solid $error-red;\n      background: $error-red-light;\n\n      @include scrollbar(\n        $scrollbar-width,\n        $error-red-light,\n        $error-red-dark,\n        $error-red-dark\n      );\n    }\n  }\n\n  &.hyper-log-stream-name {\n    // background: rgba($elegant-red, $opacity-mid);\n\n    * {\n      color: $error-red;\n    }\n\n    svg * {\n      fill: $error-red !important;\n    }\n  }\n\n  &.hyper-log {\n    .shape-log-note {\n      * {\n        color: $error-red-dark !important;\n      }\n\n      svg * {\n        fill: $error-red-dark !important;\n      }\n    }\n  }\n\n  &.timeline-log-item-wrapper {\n    background: rgba($error-red-light, $opacity-xxh);\n  }\n}\n\n.level-warn {\n  &.hyper-log-stream {\n    .hyper-log-stream-name {\n      background: rgba($warn-yellow-light, $opacity-xxh);\n    }\n\n    &.stream-expand .logs-wrapper {\n      border-top: 2px solid $warn-yellow;\n      border-bottom: 2px solid $warn-yellow;\n      background: $warn-yellow-light;\n\n      @include scrollbar(\n        $scrollbar-width,\n        $warn-yellow-light,\n        $warn-yellow-dark,\n        $warn-yellow-dark\n      );\n    }\n  }\n\n  &.hyper-log-stream-name {\n    * {\n      color: $warn-yellow-dark;\n    }\n\n    svg * {\n      fill: $warn-yellow-dark !important;\n    }\n  }\n\n  &.hyper-log {\n    .shape-log-note {\n      * {\n        color: $warn-yellow-dark !important;\n      }\n\n      svg * {\n        fill: $warn-yellow-dark !important;\n      }\n    }\n  }\n\n  &.timeline-log-item-wrapper {\n    background: rgba($warn-yellow-light, $opacity-xxh);\n  }\n}\n",".hyper-shortcuts {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: center;\n  gap: $padding-small;\n\n  position: fixed;\n  bottom: $padding-x-large;\n  left: $padding-x-large;\n  margin: 0;\n  padding: $padding-large;\n  width: 12rem;\n  height: 12rem;\n\n  // border-radius: $radius-small;\n  background: rgba($grey, $opacity-mid);\n  backdrop-filter: blur($blur-xh);\n\n  opacity: 0;\n  transition: opacity $transition-fast ease-in-out;\n  // transition-delay: $transition-normal;\n\n  pointer-events: none;\n  z-index: 999999;\n\n  &.show {\n    opacity: 1;\n  }\n\n  .shortcut {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    gap: $padding-mid;\n    pointer-events: none;\n\n    * {\n      color: $white;\n      pointer-events: none;\n    }\n  }\n\n  .shortcut-key {\n    @include font-fixed;\n    font-size: $font-size-xx-large;\n\n    border: 1px solid rgba($white, $opacity-mid);\n    padding: $padding-small $padding-mid;\n    border-radius: $radius-large;\n  }\n\n  .shortcut-description {\n    font-size: $font-size-large;\n    font-weight: $font-weight-default;\n  }\n}\n","@import 'root.scss';\n\n.hyper-log-body {\n  $divider-padding: $padding-xs;\n\n  .hyper-log-formatted-content {\n    display: flex;\n    flex-direction: column;\n    gap: $divider-padding;\n\n    .raw-content-label-wrapper {\n      display: flex;\n      flex-direction: column;\n      gap: $padding-xxs;\n\n      padding: 0;\n      margin: 0;\n\n      &:not(:last-child) {\n        padding-bottom: $divider-padding;\n        border-bottom: 1px solid rgba($grey, $opacity-xxh);\n      }\n\n      .arg {\n        display: flex;\n        gap: $padding-xs;\n\n        margin: 0;\n        padding: 0;\n      }\n\n      .raw-content {\n        @include font-fixed;\n        font-size: $font-size-small;\n        line-height: $font-size-default;\n        color: $dark-mid-grey;\n      }\n    }\n  }\